//
import React, {useState} from "react";
// 
import "./ListItem.css";

export default function ListItem() {
  const [ListVisible, setListVisible] = useState(false);
  const toggleListVisibility = () => {
    setListVisible((prevState) => !prevState);
  };

  const [isIconChanged, setIsIconChanged] = useState(null);
  const [optionsByReason, setOptionsByReason] = useState({
    " ":
      "  .        .    .",
    "   ":
      "  ,           ,       .             ' '.",
    "   ":
      "   .    .       ' '.    .",
    " ":
      "  ,  ,           .",
    " ":
      " .        .",
    " ''": "  ,    ''.",
    "   ''":
      "  ,      .     ().",
    " ":
      "               31    91.    ().",
    "   ": "  .     .",
    " ":
      "  .        .",
    " ":
      "  .      ,    ' : '.",
    " ":
      "  .        .",
    "  ":
      "   .      , .        .   .",
  });

  //     
  const handleCopyText = (fullText, index) => {
    navigator.clipboard.writeText(fullText);

    setIsIconChanged(index);

    setTimeout(() => {
      setIsIconChanged(null);
    }, 2000);
  };

  return (
    <>
      <div className="paragraph__container inline" onClick={toggleListVisibility}>
        <p> </p>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className={`arrow-icon ${ListVisible ? "open" : ""}`}
          width="8"
          height="14"
          viewBox="0 0 8 14"
          strokeWidth="2"
          fill="none"
        >
          <path d="M7 1L1 7L7 13" stroke="#F6F6F6" />
        </svg>
      </div>
      <div className="list__container">
        {ListVisible && (
          <ul>
            {Object.keys(optionsByReason).map((shortText, index) => (
              <li key={shortText} className="list__item">
                <span className="list__label" title={optionsByReason[shortText]}>
                  {shortText}
                </span>

                {/*   */}
                <div
                  className="copy__button"
                  onClick={() => handleCopyText(optionsByReason[shortText], index)}
                >
                  <svg
                    width="20"
                    height="24"
                    viewBox="0 0 20 24"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M4.66667 4.4375V3.44444C4.66667 2.09442 5.76108 1 7.11111 1H15.6667C17.0167 1 18.1111 2.09442 18.1111 3.44444V16.8889C18.1111 18.2389 17.0167 19.3333 15.6667 19.3333H14.4444M1 7.11111V20.5556C1 21.9056 2.09442 23 3.44444 23H12C13.35 23 14.4444 21.9056 14.4444 20.5556V7.11111C14.4444 5.76108 13.35 4.66667 12 4.66667H3.44444C2.09442 4.66667 1 5.76108 1 7.11111Z"
                      stroke={isIconChanged === index ? "#16ff65" : "#F6F6F6"} //     
                    />
                  </svg>
                </div>
              </li>
            ))}
          </ul>
        )}
      </div>
    </>
  );
}
