//
import React from "react";
// 
import Counter from "../../components/Counter/Counter";
import ResetCountersButton from "../../components/ResetCountersButton/ResetCountersButton";
import ListItem from "../../Components/ListItem/ListItem";
import QRGenerator from "../../Components/QRGenerator/QRGenerator";

export default function TasksPage() {
  return (
    <section className="counter__section FP">
      <ResetCountersButton
        counterIds={[
          "VSD",
          "SDIZ",
          "CHZ",
          "Marked",
          "NKMT",
          "Ordered",
          "Entered",
          "Verified",
          "Return",
          "Withdrawn",
          "documents",
          "documents__accepted",
          "documents__rejected",
          "merk",
          "Kaiten",
          "Specialists",
        ]}
      />
      <div className="counters__container">
        <div className="thematic__container">
          <div className="container__FP">
            
            <Counter id="VSD" />
          </div>
          <div className="container__FP">
            
            <Counter id="SDIZ" />
          </div>
          <div className="container__FP">
            
            <Counter id="CHZ" />
          </div>
        </div>

        <div className="container__FP">
           
          <Counter id="documents" targetIds={["documents__accepted", "documents__rejected"]} />
        </div>
        <div className="thematic__container">
          <div className="container__FP">
            
            <Counter id="documents__accepted" />
          </div>
          <div className="container__FP">
             
            <Counter id="documents__rejected" />
          </div>

          <ListItem />

          <QRGenerator />
        </div>

        <div className="thematic__container">
          <div className="container__FP">
             
            <Counter id="NKMT" />
          </div>
          <div className="container__FP">
             
            <Counter id="Ordered" />
          </div>
          <div className="container__FP">
              
            <Counter id="Entered" />
          </div>
          <div className="container__FP">
             
            <Counter id="Verified" />
          </div>
        </div>

        <div className="thematic__container">
          <div className="container__FP">
              
            <Counter id="Return" />
          </div>
          <div className="container__FP">
              
            <Counter id="Withdrawn" />
          </div>
        </div>

        <div className="container__FP">
              
          <Counter id="merk" targetIds={["Specialists", "Kaiten"]} />
        </div>
        <div className="thematic__container">
          <div className="container__FP">
            
            <Counter id="Kaiten" />
          </div>
          <div className="container__FP">
            
            <Counter id="Specialists" />
          </div>
        </div>
      </div>
    </section>
  );
}
