const ImageDownloader = () => {
  const [sheetData, setSheetData] = useState([]);
  const spreadsheetId = "1kXuikeb7rfZ8piBAEQhRq3F8JgZI0WYYbWuYmR6SjWA";
  const sheetName = process.env.REACT_APP_SHEET_NAME;

  //      Google Sheets
  const fetchReportData = () => {
    chrome.runtime.sendMessage(
      {
        action: "fetchGoogleSheetsData",
        spreadsheetId: spreadsheetId,
        sheetName: sheetName,
      },
      (response) => {
        if (response.success) {
          setSheetData(response.data); //    
        } else {
          console.error("   :", response.error);
        }
      }
    );
  };

  //       
  const handleDownload = async (imageUrl, fileName) => {
    //   
    const img = new Image();
    img.src = imageUrl;

    img.onload = function () {
      //     
      const canvas = document.createElement("canvas");
      const ctx = canvas.getContext("2d");
      canvas.width = img.width;
      canvas.height = img.height;

      //    
      ctx.drawImage(img, 0, 0);

      //   
      const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
      const data = imageData.data;

      //    ( )
      for (let i = 0; i < data.length; i += 4) {
        data[i] = 255 - data[i]; // 
        data[i + 1] = 255 - data[i + 1]; // 
        data[i + 2] = 255 - data[i + 2]; // 
      }

      //      
      ctx.putImageData(imageData, 0, 0);

      //     DataURL
      const invertedImageUrl = canvas.toDataURL("image/png");

      //    
      const link = document.createElement("a");
      link.href = invertedImageUrl;
      link.download = fileName + ".png"; //     F
      link.click();
    };
  };

  useEffect(() => {
    fetchReportData();
  }, []);

  return (
    <>
      <table className="sheet__table">
        <tbody>
          {sheetData.map((row, index) => {
            //       H     F
            const imageUrl = row[7]; //  H (  )
            const fileName = row[5]; //  F ( )

            return (
              <tr key={index}>
                <td>
                  <a href={imageUrl}>{fileName}</a>
                </td>
                <td>
                  <button onClick={() => handleDownload(imageUrl, fileName)}>
                       
                  </button>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </>
  );
};

const DataFetcher = () => {
  const [startRow, setStartRow] = useState(2); //     

  const handleProcessLinks = () => {
    chrome.runtime.sendMessage({
      type: "processLinks",
      data: {
        spreadsheetId: "1iy26IyafkroRI-uwr7WsBg9NwydCiCqVtdZv3KUOnuE",
        sheetName: "1",
        startRow,
      },
    });
  };

  return (
    <div>
      <label>
           :
        <input
          type="number"
          value={startRow}
          onChange={(e) => setStartRow(Number(e.target.value))}
        />
      </label>
      <button onClick={handleProcessLinks}></button>
    </div>
  );
};

